<script>
// コンポーネント
  import HeaderComponent from './components/common/HeaderComponent.vue';
  import WidgetComponent from './components/common/WidgetComponent.vue';
  import VideoViewer from './components/common/VideoViewer.vue';
  import MiniGame from './components/mini_game/MiniGame.vue';
  import IntroBox from './components/introduction/IntroBox.vue';
  import VRexperience from './components/VR/VRexperience.vue';
  import ChatView from './components/chat/ChatView.vue';
  import ContactView from './components/contact/ContactView.vue';
  import LoaderAnimeComponent from './components/common/LoaderAnime.vue';
  import CursorTracker from './components/common/CursorTracker.vue';
  import PlayListComponent from './components/common/PlayListComponent.vue';
  import AbatarChange from './components/introduction/AbatarChange.vue';

  // 画像
  import avatar_top from '@/assets/images/アバター22 1.png';
  import avatar_short from '@/assets/images/ミニ4 1.png';
  import avatar_minigame from '@/assets/images/ミニ5 1.png';
  import game_icon from '@/assets/images/6.png';
  import avatar_mini  from '@/assets/images/ミニ3 1.png';

  export default {
    name: 'App',
    components: {
      HeaderComponent,
      WidgetComponent,
      PlayListComponent,
      VideoViewer,
      MiniGame,
      IntroBox,
      VRexperience,
      ChatView,
      ContactView,
      LoaderAnimeComponent,
      CursorTracker,
      AbatarChange,
    },
    mounted() {
      this.$refs.headerComponent.playSound();
    },
    data() {
      return {
        showLoader: false, // 開発中false
        showdefault: true,
        showMiniGame: false,
        showShort: false,
        showVR: false,
        showIntroduction: false,
        showChat: false,
        showLearning: false,
        showContact: false,
        avatar_top,
        avatar_short,
        avatar_minigame,
        game_icon,
        currentPage: '' ,
        isPlaying: false,
        avatar_mini,
      };
    },
    methods: {
     // フェードイン
      beforeEnter(el) {
        el.style.opacity = 0;
      },
      enter(el, done) {
        el.offsetHeight;
        el.style.transition = 'opacity 0.5s';
        el.style.opacity = 1;
        this.setSound();
        done();
      },
      leave(el, done) {
        el.style.transition = 'opacity 0.5s';
        el.style.opacity = 0;
        done();
      },
      // ロードアニメ
      onLoadComplete() {
        this.showLoader = false;
        this.showdefault = true;
      },
      toggleDefault() {
        this.decisionSound()
        this.CollapseShort();
        this.CollapseMiniGame();
        this.CollapseIntroduction()

        setTimeout(() => {
          this.resetVisibility()
          this.showdefault = true;
        }, 3000);
      },
      toggleShort() {
        this.decisionSound()
        this.CollapseDefalt();
        setTimeout(() => {
          this.resetVisibility()
          this.showShort = true;
        }, 3000);
      },
      toggleMiniGame() {
        this.decisionSound()
        this.CollapseDefalt();
        this.CollapseShort();
        setTimeout(() => {
          this.resetVisibility()
          this.showMiniGame  = true;
        }, 3000);
      },
      toggleIntroduction() {
        this.decisionSound()
        this.CollapseDefalt();
        this.CollapseShort();
        setTimeout(() => {
          this.resetVisibility()
          this.showIntroduction = true;
        }, 3000);
      },
      toggleLearning() {
        this.decisionSound()
        this.CollapseDefalt();
        this.CollapseLearning();
        setTimeout(() => {
          this.resetVisibility()
          this.showLearning = true;
        }, 3000);
      },
      toggleVR() {
        this.decisionSound()
        this.CollapseDefalt();
        setTimeout(() => {
          this.resetVisibility()
          this.showVR = true;
        }, 3000);
      },
      toggleChat() {
        this.decisionSound()
        this.CollapseDefalt();
        setTimeout(() => {
          this.resetVisibility()
          this.showChat = true;
        }, 3000);
      },
      toggleContact() {
        this.decisionSound()
        this.CollapseDefalt();
        setTimeout(() => {
          this.resetVisibility()
          this.showContact = true;
        }, 3000);
      },
      resetVisibility() {
        this.showdefault = false;
        this.showIntroduction = false;
        this.showMiniGame = false;
        this.showVR = false;
        this.showChat = false;
        this.showLearning = false;
        this.showContact = false;
        this.showShort = false;

      },
      // 崩壊アニメーション
      CollapseDefalt() {
        this.$nextTick(() => {
          if (this.$refs.collapse0) {
            this.$refs.collapse0.classList.add('animate__animated', 'animate__hinge');
          }
          setTimeout(() => {
            if (this.$refs.collapse1) {
              this.$refs.collapse1.classList.add('animate__animated', 'animate__hinge');
            }
          }, 200);
          setTimeout(() => {
            if (this.$refs.collapse2) {
              this.$refs.collapse2.classList.add('animate__animated', 'animate__hinge');
            }
          }, 400);
          setTimeout(() => {
            if (this.$refs.collapse3) {
              this.$refs.collapse3.classList.add('animate__animated', 'animate__hinge');
            }
          }, 500);
          setTimeout(() => {
            if (this.$refs.collapse4) {
              this.$refs.collapse4.classList.add('animate__animated', 'animate__hinge');
            }
          }, 600);
          setTimeout(() => {
            if (this.$refs.collapse5) {
              this.$refs.collapse5.classList.add('animate__animated', 'animate__hinge');
            }
          }, 700);
        });
      },
      CollapseShort() {
        this.$nextTick(() => {
          if (this.$refs.collapse10) {
            this.$refs.collapse10.classList.add('animate__animated', 'animate__hinge');
          }
          setTimeout(() => {
            if (this.$refs.collapse11) {
              this.$refs.collapse11.classList.add('animate__animated', 'animate__hinge');
            }
          }, 200);
          setTimeout(() => {
            if (this.$refs.collapse12) {
              this.$refs.collapse12.classList.add('animate__animated', 'animate__hinge');
            }
          }, 400);
          setTimeout(() => {
            if (this.$refs.collapse13) {
              this.$refs.collapse13.classList.add('animate__animated', 'animate__hinge');
            }
          }, 500);
          setTimeout(() => {
            if (this.$refs.collapse14) {
              this.$refs.collapse14.classList.add('animate__animated', 'animate__hinge');
            }
          }, 600);
          setTimeout(() => {
            if (this.$refs.collapse15) {
              this.$refs.collapse15.classList.add('animate__animated', 'animate__hinge');
            }
          }, 700);
        });
      },
      CollapseMiniGame() {
        this.$nextTick(() => {
          if (this.$refs.collapse20) {
            this.$refs.collapse20.classList.add('animate__animated', 'animate__hinge');
          }
          setTimeout(() => {
            if (this.$refs.collapse21) {
              this.$refs.collapse21.classList.add('animate__animated', 'animate__hinge');
            }
          }, 300);
          setTimeout(() => {
            if (this.$refs.collapse22) {
              this.$refs.collapse22.classList.add('animate__animated', 'animate__hinge');
            }
          }, 400);
          setTimeout(() => {
            if (this.$refs.collapse23) {
              this.$refs.collapse23.classList.add('animate__animated', 'animate__hinge');
            }
          }, 500);
        });
      },
      CollapseIntroduction() {
        this.$nextTick(() => {
          if (this.$refs.collapse30) {
            this.$refs.collapse30.classList.add('animate__animated', 'animate__hinge');
          }
        });
      },
      CollapseLearning() {
        this.$nextTick(() => {
          if (this.$refs.collapse40) {
            this.$refs.collapse40.classList.add('animate__animated', 'animate__hinge');
          }
        });
      },
      // SE
      BGMoff() {
        if (this.$refs.headerComponent) {
          this.$refs.headerComponent.BGMoff();
        }
      },
      playSound() {
        if (this.$refs.headerComponent) {
          this.$refs.headerComponent.playSound();
        }
      },
      okSound() {
        if (this.$refs.headerComponent) {
          this.$refs.headerComponent.okSound();
        }
      },
      decisionSound() {
        if (this.$refs.headerComponent) {
          this.$refs.headerComponent.decisionSound();
        }
      },
      setSound() {
        if (this.$refs.headerComponent) {
          this.$refs.headerComponent.setSound();
        }
      },
    }
  };
</script>


