<template>
  <div>
    <div class="header-container">
      <div class="left-section">
        <div class="yagou">
          <img :src="yagou_logo" alt="屋号ロゴ" class="yagou-logo">
        </div>
        <div class="MP3">
            <p>BGM {{ isPlaying ? 'ON' : 'OFF' }}</p>
            <img :src="bgm_icon" alt="BGM_icon"  @click="toggleBGM">
        </div>
      </div>
      <div class="SNS">
        <a href="https://www.youtube.com/@byfield-up7929" target="_blank" class="sns-icon" @click="BGMoff"><img :src="youtube_icon" alt="youtube_icon"></a>
        <a href="https://www.tiktok.com/@field_up_x?lang=ja-JP" target="_blank" class="sns-icon" @click="BGMoff"><img :src="tiktok_icon" alt="tiktok_icon"></a>
        <a href="https://www.facebook.com/" target="_blank" class="sns-icon" @click="BGMoff"><img :src="facebook_icon" alt="facebook_icon" ></a>
        <a href="https://lin.ee/famO2Sr" target="_blank" class="sns-icon" @click="BGMoff"><img :src="line_icon" alt="ラインアイコン"></a>
        <a href="https://www.instagram.com/" target="_blank" class="sns-icon" @click="BGMoff"><img :src="instagram_icon" alt="instagram_icon" ></a>
        <a href="https://twitter.com/FieldUP_X" target="_blank" class="sns-icon" @click="BGMoff"><img :src="x_icon" alt="x_icon"></a>
      </div>
      <div class="hamburger" @click="toggleMenu">
        <div :class="['openbtn8', { active: isMenuOpen }]">
          <div class="openbtn-area">
            <span></span><span></span><span></span>
          </div>
        </div>
      </div>
    </div>

    <MenuComponent 
      @menu_introduction_click="handleMenuClick('introduction')"
      @menu_VR_click="handleMenuClick('VR')"
      @menu_miniGame_click="handleMenuClick('miniGame')"
      @menu_Chat_click="handleMenuClick('Chat')"
      @menu_learning_click="handleMenuClick('learning')"
      @menu_Contact_click="handleMenuClick('Contact')" 
      @stop_audio="stopAudio"
      :class="{ open: isMenuOpen }" 
      :playSound="playSound"
      :okSound="okSound"
      :BGMoff="BGMoff"
      :toggleMenu="toggleMenu"
    />
  </div>
</template>

<script>
// コンポーネント
  import MenuComponent from './MenuComponent.vue';

// 画像
  import yagou_logo from '@/assets/images/屋号ロゴ 1.png';
  import bgm_icon from '@/assets/images/icons8-周波数-50 1.png';
  import youtube_icon from '@/assets/images/icons8-youtube-50.png';
  import tiktok_icon from '@/assets/images/icons8-tiktok-50 1.png';
  import facebook_icon from '@/assets/images/icons8-フェイスブック-50 1.png';
  import instagram_icon from '@/assets/images/icons8-インスタグラム-50 1.png';
  import x_icon from '@/assets/images/icons8-ツイッターx-50 1.png';
  import line_icon from '@/assets/images/icons8-line-50 1.png';

// GBM,SE
  import bgm from '@/assets/audio/ゲームオーバーじゃない.mp3';
  import setSE1 from '@/assets/audio/select.mp3';
  import decisionSE1 from '@/assets/audio/ok.mp3';
  import setSE2 from '@/assets/audio/get.mp3';
  import decisionSE2 from '@/assets/audio/se_kuzureru03.mp3';


  export default {
    name: 'HeaderComponent',
    components: {
      MenuComponent
    },
    data() {
      return {
        yagou_logo,
        bgm_icon,
        youtube_icon,
        tiktok_icon,
        facebook_icon,
        instagram_icon,
        line_icon,
        x_icon,
        isMenuOpen: false,
        isPlaying: false,
        audio: null,
      };
    },
    methods: {
      toggleMenu() {
        this.okSound()
        this.isMenuOpen = !this.isMenuOpen;
      },
      handleMenuClick(menuItem) {
        this.$emit(`${menuItem}_click`);
      },
      toggleBGM() {
        if (this.audio === null) {
          this.audio = new Audio(bgm);
          this.audio.volume = 0.05;
        }

        if (this.isPlaying) {
          this.audio.pause();
          this.audio.currentTime = 0;
          this.isPlaying = false;
        } else {
          this.audio.play();
          this.isPlaying = true;
        }
      },
      BGMoff() {
        this.isPlaying = false;
        if (this.audio) {
          this.audio.pause();
          this.audio.currentTime = 0;
        }
      },
      playSound() {
        if (this.isPlaying) {
          const hoverSound = new Audio(setSE1);
          hoverSound.volume = 0.15;
          hoverSound.play().catch(error => {
            console.error("Audio play failed: ", error);
          });
        }
      },
      okSound() {
        if (this.isPlaying) {
          const hoverSound = new Audio(decisionSE1);
          hoverSound.volume = 0.1;
          hoverSound.play().catch(error => {
            console.error("Audio play failed: ", error);
          });
        }
      },
      setSound() {
        if (this.isPlaying) {
          const hoverSound = new Audio(setSE2);
          hoverSound.volume = 0.05;
          hoverSound.play().catch(error => {
            console.error("Audio play failed: ", error);
          });
        }
      },
      decisionSound() {
        if (this.isPlaying) {
          const hoverSound = new Audio(decisionSE2);
          hoverSound.volume = 0.02;
          hoverSound.play().catch(error => {
            console.error("Audio play failed: ", error);
          });
        }
      }

    }
  }
  </script>

<style>

