<template>
    <div class="playlist_container">
      <h2>PlayList</h2>
      <div class="col-12 mb-3" v-for="(playlist, index) in playlists" :key="index">
        <a :href="playlist.url" target="_blank" class="btn btn-gradients w-100" @mouseover="PlaySound" @click="OkSound">
          <span>{{ playlist.name }}</span>
        </a>
      </div>
    </div>
  </template>
  
  <script>
  import { getPlaylists } from './youtube_api';
  
  export default {
    props: {
      videoType: String ,
      playSound: Function,
      okSound: Function,
      decisionSound: Function,
      setSound: Function,
      BGMoff: Function
    },
    data() {
      return {
        playlists: [],
        isPlaying: true, 
        audio: new Audio('path/to/sound.mp3')
      }
    },
    created() {
      this.fetchPlaylists();
    },
    methods: {
      async fetchPlaylists() {
          const data = await getPlaylists();
          if (this.videoType == 'main') {
              this.playlists = data.mainPlaylists;
          } else if (this.videoType == 'short') {
              this.playlists = data.shortPlaylists;
          }
      },
      // SE
      PlaySound() {
        if (this.playSound) {
          this.playSound();
        }
      },
      OkSound() {
        if (this.okSound) {
          this.okSound();
          this.BGMoff();
        }
      },

    }
  }
  </script>
  
  

