<template>
  <transition name="fade-first" @after-leave="startSecondTransition">
    <div v-if="loadingfirst" :class="{'loader': true, 'loader-fade-out': !loadingfirst}">
      <video 
        :src="videoSrc" 
        @ended="handleVideoEnd" 
        autoplay 
        muted
        class="loading-video"
      ></video>
      <div class="loading-text silkscreen-bold">Loading...</div>
    </div>
  </transition>

  <transition name="fade-second" @before-enter="prepareSecondTransition" @after-enter="finishLoading">
    <div v-show="loadingsecond" :class="{'loader': true, 'loader-fade-out': !loadingsecond}">
      <div class="send-text silkscreen-bold">
        <span v-for="(char, index) in splitText" :key="index" :style="{ animationDelay: `${index * 0.1}s` }" class="char">
          {{ char }}
        </span>
      </div>
    </div>
  </transition>
</template>

<script>
import videoFile from '@/assets/videos/椅子で近づく.mp4';

export default {
  name: 'LoaderAnime',
  data() {
    return {
      loadingfirst: true, 
      loadingsecond: false, 
      videoSrc: videoFile,
      splitText: [], 
    };
  },
  methods: {
    handleVideoEnd() {
      console.log('Video ended');
      this.loadingfirst = false;
    },
    startSecondTransition() {
      console.log('Starting second transition');
      this.loadingsecond = true;
      this.splitText = "want to learn?".split('').map(char => char === ' ' ? '\u00A0' : char);
    },
    prepareSecondTransition() {
      console.log('Preparing second transition');
    },
    finishLoading() {
      console.log('Finishing loading');
      setTimeout(() => {
        console.log('Timeout completed');
        this.loadingsecond = false;
        this.$emit('loaded');
      }, 2500);
    },
  }
};
</script>

<style>
/* フォントデータ */
.silkscreen-bold {
  font-family: "Silkscreen", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.loader {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: #000000;
  z-index: 555;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  transition: opacity 0.8s ease-out; 
  overflow: hidden; 
}

.loading-video {
  width: 100%;
  height: 100%;
  object-fit: contain; 
}

.loading-text {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  color: #ffffff;
  font-size: 4rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7); 
  font-family: "Silkscreen", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.send-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ffffff;
  font-size: 4rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  font-family: "Silkscreen", sans-serif;
  font-weight: 400;
  font-style: normal;
  
  white-space: nowrap; 
}
.char {
  display: inline-block;
  opacity: 0;
  transform: translateY(20px);
  animation: textAnimation 0.8s ease-out forwards;
}
@keyframes textAnimation {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-text {
  animation: textAnimation 0.8s ease-out forwards;
}

@keyframes textAnimation {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) translateY(80px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, -50%) translateY(0);
  }
}


.fade-first-enter-active, .fade-first-leave-active {
  transition: opacity 0.8s ease-out;
}
.fade-first-enter, .fade-first-leave-to {
  opacity: 0;
}

.fade-second-enter-active, .fade-second-leave-active {
  transition: opacity 0.8s ease-out;
}
.fade-second-enter, .fade-second-leave-to {
  opacity: 0;
}

</style>
