<template>
    <div>
        <div id="cursor" :style="cursorStyle"></div>
        <div id="stalker" :style="stalkerStyle"></div>
  </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        cursorX: 0,
        cursorY: 0,
        stalkerX: 0,
        stalkerY: 0,
        cursorActive: false,
      };
    },
    computed: {
        cursorStyle() {
        return {
            opacity: "0.9",
            top: `${this.cursorY}px`,
            left: `${this.cursorX}px`,
            transform: this.cursorActive ? "scale(1.4)" : "",
        };
        },
        stalkerStyle() {
        return {
            opacity: "0.4",
            top: `${this.stalkerY}px`,
            left: `${this.stalkerX}px`,
            transform: this.cursorActive ? "scale(1.4)" : "",
        };
        },
    },
    mounted() {
        document.addEventListener("mousemove", this.handleMouseMove);
    },
    methods: {
        handleMouseMove(e) {
        this.cursorX = e.clientX;
        this.cursorY = e.clientY;
        setTimeout(() => {
            this.stalkerX = e.clientX;
            this.stalkerY = e.clientY;
        }, 140);
        },
        handleMouseEnter() {
        this.cursorActive = true;
        },
        handleMouseLeave() {
        this.cursorActive = false;
        },
    },
    beforeUnmount() {
      document.removeEventListener("mousemove", this.handleMouseMove);
    },
  };
  </script>
  
  <style scoped>
  #cursor {
    position: fixed;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px 2px rgb(255, 255, 255);
    animation: glow-c 1.5s infinite alternate;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    margin: -10px 0 0 -10px;
    z-index: 9999;
    pointer-events: none;
    opacity: 0;
    transition: transform 0.1s;
  }
  @keyframes glow-c {
    0% {
        box-shadow: 0 0 5px 2px rgb(255, 255, 255);
    }
    100% {
        box-shadow: 0 0 20px 5px rgb(240, 255, 255);
    }
    }
  
  #stalker {
    position: fixed;
    background: transparent;
    border: 2px solid aqua;
    box-shadow: 0 0 10px 2px aqua;
    animation: glow-o 1.5s infinite alternate;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    border-radius: 30px;
    margin: -30px 0 0 -30px;
    z-index: 9998;
    pointer-events: none;
    opacity: 0;
    transition: transform 0.5s;
  }
  @keyframes glow-o {
    0% {
        box-shadow: 0 0 5px 2px aqua;
    }
    100% {
        box-shadow: 0 0 20px 5px aqua;
    }
    }
  
  </style>
  