<template>
  <transition name="slide-down">
    <div v-if="isVisible" class="widget-container">
      <p @click="toggleSelf">NEW YoutubeClip</p>
        <ul>
          <li v-for="(video, index) in videos.slice(0, 6)" :key="index" @mouseover="PlaySound" @click="OkSound">
            <a :href="video.url" target="_blank">{{ video.date.split('T')[0] }} {{ video.title }}</a>
          </li>
        </ul>
    </div>
  </transition>
  <transition name="slide-up">
    <div v-if="!isVisible" class="widget-container">
      <button @click="toggleSelf">NEW YoutubeClip</button>
    </div>
  </transition>


</template>

<script>
import { getVideosData } from './youtube_api.js';

export default {
  props: {
    videoType: String,
    playSound: Function,
    okSound: Function,
    decisionSound: Function,
    setSound: Function,
    BGMoff: Function,
  },
  data() {
    return {
      videos: [],
      isVisible: true
    };
  },
  created() {
    this.fetchVideos();
  },
  methods: {
    async fetchVideos() {
      const now = Date.now();
      if (this.lastFetchTime && now - this.lastFetchTime < 60000) {
        this.isLoading = false; // キャッシュがある場合もローディングを終了
        return;
      }

      this.isLoading = true; // データ取得中にローディングを表示

      const data = await getVideosData();
      this.videos = this.videoType === 'main' ? data.mainVideos : data.shortVideos;
      this.lastFetchTime = now;
      this.isLoading = false; // データ取得完了後にローディングを終了
    },
    toggleSelf() {
      this.isVisible = !this.isVisible; 
    },
    // SE
    PlaySound() {
      if (this.playSound) {
        this.playSound();
      }
    },
    OkSound() {
      if (this.okSound) {
        if (this.okSound) {
          this.okSound();
        }
        if (this.BGMoff) {
          console.log('BGMoff function called');
          this.BGMoff();
        }
      }
    },
  }
}
  </script>




