<template>
  <div id="gameCanvasContainer" class="container">
    <canvas ref="gameCanvas" class="responsive-canvas"></canvas>
  </div>
</template>

<script>
import Phaser from 'phaser';
import MyScene from './MyScene';

export default {
  name: 'MiniGame',
  mounted() {
    this.initializeGame();
    window.addEventListener('resize', this.resizeGame); 
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.resizeGame); 
  },
  methods: {
    initializeGame() {
      console.log('ゲームの初期化...');
      console.log(this.$refs.gameCanvas);
      if (this.$refs.gameCanvas) {
        this.game = new Phaser.Game({
          type: Phaser.CANVAS,
          parent: 'gameCanvasContainer',
          canvas: this.$refs.gameCanvas,
          scene: MyScene,
          physics: {
            default: 'matter',
            matter: {
              gravity: { y: 0.5 }
            }
          },
          backgroundColor: '#ffffff'
        });
        this.resizeGame(); 
      } else {
        console.error('Canvasの参照が取得できません');
      }
    },
    resizeGame() {
      if (this.game) {
        const canvas = this.$refs.gameCanvas;
        const width = window.innerWidth / 2; 
        const height = window.innerHeight / 2; 

        // 確認用
        console.log(`キャンバスのサイズを ${width}px x ${height}px に変更します`);

        canvas.width = width;
        canvas.height = height;
        this.game.scale.resize(width, height);
      }
    }
  }
};
</script>

<style>
#gameCanvasContainer {
  width: 100%; 
  height: 100%;
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
